import pool from '@/app/database';
import { NextResponse } from 'next/server';

export async function GET(_request: Request,
  { params }: { params: { nombre: string } }
) {
  const nombre = params.nombre;
  console.log("Nombre: %s", nombre);


  if (!nombre || nombre.trim() === '') {
    return NextResponse.json({ error: 'Nombre inv√°lido' }, { status: 400 });
  }

  try {
    const [rows]: any = await pool.query('SELECT * FROM usuarios WHERE nombre = ?', [nombre]);

    if (rows.length === 0) {
      return NextResponse.json({ error: 'usuario no encontrado' }, { status: 404 });
    }

    return NextResponse.json(rows[0]);
  } catch (error: any) {
    console.error('Error en la consulta:', error);
    return NextResponse.json(
      { error: 'Error en la consulta', details: error.message },
      { status: 500 }
    );
  }
}
